* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

h1, h2, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.country-list__item {
  border-radius: 5px;
  align-items: center;
  gap: 10px;
  display: flex;
}

.country-list__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

.country-list__img-box {
  width: 50px;
  height: 40px;
  border-radius: 5px;
  overflow: hidden;
}

.card__box {
  align-items: center;
  display: flex;
}

.card__img {
  width: 55px;
  height: 40px;
  border-radius: 5px;
}

.card__name {
  margin-left: 15px;
  font-size: 30px;
}

.card__capital, .card__population, .card__languages {
  margin-top: 15px;
  font-size: 20px;
  font-weight: 500;
}

